import React, { useState, useCallback, useRef, useEffect } from 'react'
import { Edit3, Save, X, RotateCcw } from 'lucide-react'

/**
 *    
 * -   
 * - / 
 * -     (text, textarea, select)
 * -  
 */
export default function EditableText({
  value,
  onSave,
  onCancel,
  placeholder = ' ...',
  type = 'text', // 'text', 'textarea', 'select'
  options = [], // select   
  className = '',
  editClassName = '',
  displayClassName = '',
  disabled = false,
  maxLength,
  rows = 3,
  autoFocus = true,
  validator,
  formatDisplay,
  renderDisplay,
  editable = true,
  children
}) {
  const [isEditing, setIsEditing] = useState(false)
  const [editValue, setEditValue] = useState(value || '')
  const [validationError, setValidationError] = useState('')
  const inputRef = useRef(null)

  //  
  const startEditing = useCallback(() => {
    if (disabled || !editable) return
    setIsEditing(true)
    setEditValue(value || '')
    setValidationError('')
  }, [disabled, editable, value])

  //  
  const saveEditing = useCallback(() => {
    //  
    if (validator) {
      const error = validator(editValue)
      if (error) {
        setValidationError(error)
        return
      }
    }

    //    
    if (editValue !== value) {
      onSave?.(editValue)
    }
    
    setIsEditing(false)
    setValidationError('')
  }, [editValue, value, validator, onSave])

  //  
  const cancelEditing = useCallback(() => {
    setIsEditing(false)
    setEditValue(value || '')
    setValidationError('')
    onCancel?.()
  }, [value, onCancel])

  // Enter  
  const handleKeyDown = useCallback((e) => {
    if (e.key === 'Enter' && type !== 'textarea') {
      e.preventDefault()
      saveEditing()
    } else if (e.key === 'Escape') {
      e.preventDefault()
      cancelEditing()
    }
  }, [type, saveEditing, cancelEditing])

  //     
  useEffect(() => {
    if (isEditing && autoFocus && inputRef.current) {
      inputRef.current.focus()
      if (type === 'text' || type === 'textarea') {
        inputRef.current.select()
      }
    }
  }, [isEditing, autoFocus, type])

  //   
  const displayValue = formatDisplay ? formatDisplay(value) : value

  if (isEditing) {
    return (
      <div className={`inline-block w-full ${className}`}>
        <div className="flex items-start space-x-2">
          <div className="flex-1">
            {type === 'textarea' ? (
              <textarea
                ref={inputRef}
                value={editValue}
                onChange={(e) => setEditValue(e.target.value)}
                onKeyDown={handleKeyDown}
                placeholder={placeholder}
                maxLength={maxLength}
                rows={rows}
                className={`w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-blue-500 resize-none ${editClassName}`}
              />
            ) : type === 'select' ? (
              <select
                ref={inputRef}
                value={editValue}
                onChange={(e) => setEditValue(e.target.value)}
                onKeyDown={handleKeyDown}
                className={`w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-blue-500 ${editClassName}`}
              >
                {options.map((option) => (
                  <option key={option.value} value={option.value}>
                    {option.label}
                  </option>
                ))}
              </select>
            ) : (
              <input
                ref={inputRef}
                type="text"
                value={editValue}
                onChange={(e) => setEditValue(e.target.value)}
                onKeyDown={handleKeyDown}
                placeholder={placeholder}
                maxLength={maxLength}
                className={`w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-blue-500 ${editClassName}`}
              />
            )}
            
            {/*    */}
            {validationError && (
              <p className="mt-1 text-sm text-red-600">{validationError}</p>
            )}
            
            {/*    */}
            {maxLength && (
              <p className="mt-1 text-xs text-gray-500 text-right">
                {editValue.length}/{maxLength}
              </p>
            )}
          </div>

          {/*   */}
          <div className="flex items-center space-x-1">
            <button
              onClick={saveEditing}
              className="p-2 text-green-600 hover:bg-green-50 rounded-lg transition-colors"
              title=""
            >
              <Save className="w-4 h-4" />
            </button>
            <button
              onClick={cancelEditing}
              className="p-2 text-gray-600 hover:bg-gray-50 rounded-lg transition-colors"
              title=""
            >
              <X className="w-4 h-4" />
            </button>
          </div>
        </div>
      </div>
    )
  }

  return (
    <div className={`group inline-block w-full ${className}`}>
      <div className="flex items-start justify-between">
        <div className={`flex-1 ${displayClassName}`}>
          {renderDisplay ? (
            renderDisplay(displayValue)
          ) : children ? (
            children
          ) : (
            <span className={displayValue ? '' : 'text-gray-400 italic'}>
              {displayValue || placeholder}
            </span>
          )}
        </div>

        {/*   */}
        {editable && !disabled && (
          <button
            onClick={startEditing}
            className="ml-2 p-1 text-gray-400 hover:text-blue-600 hover:bg-blue-50 rounded opacity-0 group-hover:opacity-100 transition-all"
            title=""
          >
            <Edit3 className="w-4 h-4" />
          </button>
        )}
      </div>
    </div>
  )
}

/**
 *     
 */
export function EditableListItem({ 
  items = [], 
  onItemsChange, 
  placeholder = ' ...', 
  maxItems = 10,
  allowEmpty = false 
}) {
  const [editingIndex, setEditingIndex] = useState(null)
  const [newItem, setNewItem] = useState('')
  const [isAdding, setIsAdding] = useState(false)

  //  
  const updateItem = useCallback((index, newValue) => {
    if (!newValue.trim() && !allowEmpty) {
      return
    }
    
    const updatedItems = [...items]
    updatedItems[index] = newValue.trim()
    onItemsChange?.(updatedItems)
    setEditingIndex(null)
  }, [items, onItemsChange, allowEmpty])

  //  
  const removeItem = useCallback((index) => {
    const updatedItems = items.filter((_, i) => i !== index)
    onItemsChange?.(updatedItems)
  }, [items, onItemsChange])

  //  
  const addItem = useCallback(() => {
    if (!newItem.trim()) {
      setIsAdding(false)
      return
    }
    
    const updatedItems = [...items, newItem.trim()]
    onItemsChange?.(updatedItems)
    setNewItem('')
    setIsAdding(false)
  }, [items, newItem, onItemsChange])

  return (
    <div className="space-y-2">
      {/*   */}
      {items.map((item, index) => (
        <div key={index} className="flex items-center space-x-2 group">
          <div className="flex-1">
            <EditableText
              value={item}
              onSave={(value) => updateItem(index, value)}
              placeholder={placeholder}
              className="w-full"
            />
          </div>
          
          <button
            onClick={() => removeItem(index)}
            className="p-1 text-red-400 hover:text-red-600 hover:bg-red-50 rounded opacity-0 group-hover:opacity-100 transition-all"
            title=""
          >
            <X className="w-4 h-4" />
          </button>
        </div>
      ))}

      {/*    */}
      {isAdding ? (
        <div className="flex items-center space-x-2">
          <input
            type="text"
            value={newItem}
            onChange={(e) => setNewItem(e.target.value)}
            onKeyDown={(e) => {
              if (e.key === 'Enter') {
                e.preventDefault()
                addItem()
              } else if (e.key === 'Escape') {
                setIsAdding(false)
                setNewItem('')
              }
            }}
            placeholder={placeholder}
            className="flex-1 px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-blue-500"
            autoFocus
          />
          <button
            onClick={addItem}
            className="p-2 text-green-600 hover:bg-green-50 rounded-lg transition-colors"
            title=""
          >
            <Save className="w-4 h-4" />
          </button>
          <button
            onClick={() => {
              setIsAdding(false)
              setNewItem('')
            }}
            className="p-2 text-gray-600 hover:bg-gray-50 rounded-lg transition-colors"
            title=""
          >
            <X className="w-4 h-4" />
          </button>
        </div>
      ) : (
        items.length < maxItems && (
          <button
            onClick={() => setIsAdding(true)}
            className="w-full py-2 border-2 border-dashed border-gray-300 text-gray-500 hover:border-blue-300 hover:text-blue-600 rounded-lg transition-colors"
          >
            +  
          </button>
        )
      )}
    </div>
  )
}