//   
import { CONSTANTS } from './constants'

/**
 *    
 * @param {string} text -  
 * @param {number} minLength -  
 * @param {number} maxLength -  
 * @returns {object}   
 */
export const validateTextLength = (text, minLength = CONSTANTS.TEXT_LENGTH.MIN, maxLength = CONSTANTS.TEXT_LENGTH.MAX) => {
  if (!text || text.trim().length === 0) {
    return {
      isValid: false,
      error: CONSTANTS.ERROR_MESSAGES.EMPTY_TEXT
    }
  }

  const length = text.trim().length

  if (length < minLength) {
    return {
      isValid: false,
      error: ` ${minLength}  . (: ${length})`
    }
  }

  if (length > maxLength) {
    return {
      isValid: false,
      error: ` ${maxLength}  . (: ${length})`
    }
  }

  return {
    isValid: true,
    length
  }
}

/**
 *   
 * @param {File} file -  
 * @returns {object}   
 */
export const validateFile = (file) => {
  if (!file) {
    return {
      isValid: false,
      error: ' .'
    }
  }

  //   
  if (file.size > CONSTANTS.FILE_UPLOAD.MAX_SIZE) {
    return {
      isValid: false,
      error: CONSTANTS.ERROR_MESSAGES.FILE_SIZE
    }
  }

  //   
  const fileExtension = '.' + file.name.split('.').pop().toLowerCase()
  if (!CONSTANTS.FILE_UPLOAD.ACCEPTED_TYPES.includes(fileExtension)) {
    return {
      isValid: false,
      error: `${CONSTANTS.ERROR_MESSAGES.FILE_TYPE}  : ${CONSTANTS.FILE_UPLOAD.ACCEPTED_TYPES.join(', ')}`
    }
  }

  return {
    isValid: true,
    fileType: fileExtension,
    fileName: file.name,
    fileSize: file.size
  }
}

/**
 * URL  
 * @param {string} url -  URL
 * @returns {object}   
 */
export const validateURL = (url) => {
  if (!url || url.trim().length === 0) {
    return {
      isValid: false,
      error: 'URL .'
    }
  }

  try {
    const urlObj = new URL(url)
    if (!['http:', 'https:'].includes(urlObj.protocol)) {
      return {
        isValid: false,
        error: ' URL  . (http://  https://  )'
      }
    }
  } catch (error) {
    return {
      isValid: false,
      error: ' URL  .'
    }
  }

  return {
    isValid: true,
    url: url.trim()
  }
}

/**
 *    
 * @param {number} count -  
 * @param {string} type -   ('vocabulary' or 'reading')
 * @returns {object}   
 */
export const validateProblemCount = (count, type) => {
  const limits = type === 'vocabulary' 
    ? CONSTANTS.PROBLEM_COUNT.VOCABULARY 
    : CONSTANTS.PROBLEM_COUNT.READING

  if (!count || count < limits.MIN) {
    return {
      isValid: false,
      error: ` ${limits.MIN}  .`
    }
  }

  if (count > limits.MAX) {
    return {
      isValid: false,
      error: ` ${limits.MAX}  .`
    }
  }

  return {
    isValid: true,
    count
  }
}

/**
 *    
 * @param {string} lengthStr -   
 * @returns {object}   
 */
export const validateCustomLength = (lengthStr) => {
  const length = parseInt(lengthStr, 10)

  if (isNaN(length)) {
    return {
      isValid: false,
      error: ' .'
    }
  }

  if (length < CONSTANTS.TEXT_LENGTH.MIN) {
    return {
      isValid: false,
      error: ` ${CONSTANTS.TEXT_LENGTH.MIN}  .`
    }
  }

  if (length > CONSTANTS.TEXT_LENGTH.MAX) {
    return {
      isValid: false,
      error: ` ${CONSTANTS.TEXT_LENGTH.MAX}  .`
    }
  }

  return {
    isValid: true,
    length
  }
}