//   -      

class DemoService {
  constructor() {
    this.delay = 1000; //  API  1 
  }

  //   
  async simulateDelay() {
    return new Promise(resolve => setTimeout(resolve, this.delay));
  }

  //    
  async adminLogin(email, password) {
    await this.simulateDelay();
    
    if (email === 'admin@inblanq.com' && password === '2025') {
      return {
        success: true,
        token: 'demo-admin-token-12345',
        user: {
          id: 'demo-admin-id',
          email: 'admin@inblanq.com',
          role: 'admin'
        }
      };
    } else {
      return {
        success: false,
        error: 'Invalid credentials'
      };
    }
  }

  // AI    
  async generateContent(provider, contentType, prompt, options = {}) {
    await this.simulateDelay();

    const { targetAge = 'elem2', difficulty = 'beginner', contentLength = '800' } = options;

    //   (  )
    const topic = this.extractTopic(prompt);

    //  
    if (contentType === 'reading') {
      const topicData = this.getTopicData(topic, targetAge);
      
      return {
        success: true,
        content: {
          title: topicData.title,
          description: `${this.getAgeDescription(targetAge)}  ${topicData.topic} `,
          mainContent: {
            introduction: this.generateReadingContent(contentLength, targetAge, topic),
            keyPoints: topicData.keyPoints,
            examples: topicData.examples
          }
        },
        provider: provider,
        timestamp: new Date().toISOString(),
        tokensUsed: 150
      };
    }

    //  
    if (contentType === 'vocabulary') {
      return {
        success: true,
        content: {
          title: "  ",
          vocabularyList: [
            {
              word: "",
              meaning: "  ",
              synonyms: ["", ""],
              antonyms: ["", ""],
              difficulty: "",
              example: " ."
            },
            {
              word: "",
              meaning: " ",
              synonyms: ["", ""],
              antonyms: [""],
              difficulty: "", 
              example: " ."
            },
            {
              word: "",
              meaning: "  ",
              synonyms: ["", ""],
              antonyms: ["", ""],
              difficulty: "",
              example: "  ."
            }
          ]
        },
        provider: provider,
        timestamp: new Date().toISOString(),
        tokensUsed: 100
      };
    }

    //  
    if (contentType === 'questions') {
      return {
        success: true,
        content: {
          title: " ",
          questions: [
            {
              type: " ",
              question: "    3 .",
              answerSpace: 3,
              points: 10
            },
            {
              type: " ", 
              question: "      .",
              answerSpace: 4,
              points: 15
            },
            {
              type: " ",
              question: "     .",
              answerSpace: 3,
              points: 10
            },
            {
              type: " ",
              question: "     .",
              answerSpace: 5,
              points: 20
            },
            {
              type: " ",
              question: "      .",
              answerSpace: 4,
              points: 15
            },
            {
              type: " ",
              question: "      .",
              answerSpace: 5,
              points: 20
            }
          ]
        },
        provider: provider,
        timestamp: new Date().toISOString(),
        tokensUsed: 120
      };
    }

    //  
    if (contentType === 'answers') {
      return {
        success: true,
        content: {
          title: " ",
          answers: [
            {
              questionNumber: 1,
              correctAnswer: ", , ",
              explanation: "    (), (), (, ) .",
              gradingCriteria: ["3    ", " "],
              tips: "     ."
            },
            {
              questionNumber: 2,
              correctAnswer: "    .",
              explanation: "        .",
              gradingCriteria: ["  ", " "],
              tips: "    ."
            }
          ]
        },
        provider: provider,
        timestamp: new Date().toISOString(),
        tokensUsed: 90
      };
    }

    //  
    return {
      success: true,
      content: {
        title: " ",
        description: "    ."
      },
      provider: provider,
      timestamp: new Date().toISOString(),
      tokensUsed: 50
    };
  }

  //   
  getAgeDescription(targetAge) {
    const ageMap = {
      elem1: ' 1',
      elem2: ' 2',
      elem3: ' 3',
      elem4: ' 4',
      elem5: ' 5',
      elem6: ' 6',
      middle1: ' 1',
      middle2: ' 2',
      middle3: ' 3',
      high1: ' 1',
      high2: ' 2',
      high3: ' 3'
    };
    return ageMap[targetAge] || ' 2';
  }

  //   ()
  extractTopic(prompt) {
    if (!prompt) return 'game';
    
    const promptLower = prompt.toLowerCase();
    
    if (promptLower.includes('') || promptLower.includes('') || promptLower.includes('') || promptLower.includes('')) {
      return 'game';
    } else if (promptLower.includes('') || promptLower.includes('') || promptLower.includes('')) {
      return 'sports';
    } else if (promptLower.includes('') || promptLower.includes('') || promptLower.includes('')) {
      return 'music';
    } else if (promptLower.includes('') || promptLower.includes('') || promptLower.includes('')) {
      return 'nature';
    } else if (promptLower.includes('') || promptLower.includes('') || promptLower.includes('')) {
      return 'science';
    } else if (promptLower.includes('') || promptLower.includes('') || promptLower.includes('')) {
      return 'food';
    } else {
      return 'game'; // 
    }
  }

  //   
  getTopicData(topic, targetAge) {
    const topicMap = {
      game: {
        title: " ",
        topic: " & ",
        keyPoints: [
          "   ",
          "    ", 
          "     "
        ],
        examples: [
          {
            korean: "     .",
            explanation: "  "
          }
        ]
      },
      sports: {
        title: " ",
        topic: "",
        keyPoints: [
          "    ",
          "   ",
          " "
        ],
        examples: [
          {
            korean: "    .",
            explanation: "  "
          }
        ]
      },
      nature: {
        title: " ",
        topic: " & ",
        keyPoints: [
          "    ",
          "    ",
          "  "
        ],
        examples: [
          {
            korean: "    .",
            explanation: "  "
          }
        ]
      }
    };

    return topicMap[topic] || topicMap.game;
  }

  //      
  generateReadingContent(contentLength, targetAge, topic = 'nature') {
    const contentMap = {
      game: {
        base: "    .        .       ,     .        .        .      .",
        additional: "     .       .         .       .      .",
        extra: "    .         AI .       .         ."
      },
      nature: {
        base: "       .    ,    .         .      .    .     .       .     .",
        additional: "      .       .       .       .  ,      .     .",
        extra: "    ,     .      .             .         ."
      },
      sports: {
        base: "     . , ,      .      .      .      .       .",
        additional: "       .          .            .",
        extra: "       .     .           ."
      }
    };

    const content = contentMap[topic] || contentMap.nature;
    const { base, additional, extra } = content;

    const targetLength = parseInt(contentLength);
    let result = base;

    if (targetLength >= 600) {
      result += additional;
    }
    if (targetLength >= 1000) {
      result += extra;
    }

    //    
    if (result.length < targetLength) {
      const paddingMap = {
        game: "       .",
        nature: "      .",
        sports: "     ."
      };
      const padding = (paddingMap[topic] || paddingMap.nature).repeat(
        Math.ceil((targetLength - result.length) / 30)
      );
      result += padding;
    }

    return result.substring(0, targetLength);
  }

  //    
  async getStats() {
    await this.simulateDelay();
    
    return {
      success: true,
      stats: {
        totalGenerations: 42,
        totalTokens: 3580,
        providerBreakdown: {
          claude: { count: 25, tokens: 2100 },
          gemini: { count: 17, tokens: 1480 }
        },
        contentTypeBreakdown: {
          reading: { count: 15, tokens: 1800 },
          vocabulary: { count: 12, tokens: 980 },
          questions: { count: 10, tokens: 600 },
          answers: { count: 5, tokens: 200 }
        },
        dailyUsage: {
          '2025-08-01': { count: 8, tokens: 720 },
          '2025-07-31': { count: 12, tokens: 1040 },
          '2025-07-30': { count: 10, tokens: 850 }
        }
      }
    };
  }
}

export default DemoService;