import { useState, useCallback, useRef } from 'react'

/**
 * API    
 * @param {Function} apiFunction -  API 
 * @returns {Object} API   
 */
export function useApiCall(apiFunction) {
  const [data, setData] = useState(null)
  const [loading, setLoading] = useState(false)
  const [error, setError] = useState(null)
  const abortControllerRef = useRef(null)

  const execute = useCallback(async (...args) => {
    //     
    if (abortControllerRef.current) {
      abortControllerRef.current.abort()
    }
    
    abortControllerRef.current = new AbortController()
    setLoading(true)
    setError(null)
    
    try {
      const result = await apiFunction(...args, { signal: abortControllerRef.current.signal })
      setData(result)
      return result
    } catch (err) {
      if (err.name === 'AbortError') {
        return //      
      }
      const errorMessage = err.response?.data?.message || err.message || '    '
      setError(errorMessage)
      throw err
    } finally {
      setLoading(false)
      abortControllerRef.current = null
    }
  }, [apiFunction])

  const reset = useCallback(() => {
    setData(null)
    setError(null)
    setLoading(false)
  }, [])

  return { data, loading, error, execute, reset }
}

/**
 *  API     
 * @returns {Object}  API   
 */
export function useParallelApiCalls() {
  const [loading, setLoading] = useState(false)
  const [errors, setErrors] = useState([])

  const executeAll = useCallback(async (apiCalls) => {
    setLoading(true)
    setErrors([])
    
    try {
      const results = await Promise.allSettled(apiCalls)
      
      const successResults = []
      const failedResults = []
      
      results.forEach((result, index) => {
        if (result.status === 'fulfilled') {
          successResults.push(result.value)
        } else {
          failedResults.push({
            index,
            error: result.reason?.message || '   '
          })
        }
      })
      
      if (failedResults.length > 0) {
        setErrors(failedResults)
      }
      
      return { 
        success: successResults, 
        failed: failedResults,
        hasErrors: failedResults.length > 0
      }
    } finally {
      setLoading(false)
    }
  }, [])

  return { loading, errors, executeAll }
}