import React, { useState, useCallback } from 'react'
import { 
  BookOpen, 
  Sparkles, 
  FileText, 
  Upload, 
  Download,
  ArrowRight,
  RefreshCw,
  Globe
} from 'lucide-react'
import aiService from '../services/aiService'
import { config } from '../config'
import { TOPIC_CATEGORIES, GRADE_OPTIONS, LENGTH_OPTIONS } from '../utils/constants'
import { validateFile } from '../utils/validation'
import { useApiCall, useParallelApiCalls } from '../hooks/useApiCall'
import { useTextAnalysis } from '../hooks/useTextAnalysis'
import { generatePDF } from '../utils/pdfGenerator'
import { getAvailableProviders } from '../config/apiKeys'
import ErrorMessage from '../components/common/ErrorMessage'
import AnalysisChart from '../components/literacy/AnalysisChart'
import ProblemCard from '../components/literacy/ProblemCard'
import TextDisplay from '../components/literacy/TextDisplay'
import VocabularyExtractor from '../components/literacy/VocabularyExtractor'
import ProblemGenerator from '../components/literacy/ProblemGenerator'

export default function ReadingTrainerPage() {
  const [mode, setMode] = useState('generate') // 'generate' or 'input'
  const [step, setStep] = useState(1) // 1: , 2: , 3: 
  
  //   
  const [selectedTopic, setSelectedTopic] = useState('')
  const [customTopic, setCustomTopic] = useState('')
  const [selectedGrade, setSelectedGrade] = useState('elem4')
  const [selectedLength, setSelectedLength] = useState('800')
  const [customLength, setCustomLength] = useState('')
  const [selectedProvider, setSelectedProvider] = useState('claude')
  
  //   AI 
  const availableProviders = getAvailableProviders()
  
  //   
  const [generatedText, setGeneratedText] = useState('')
  const [userText, setUserText] = useState('')
  const [errorMessage, setErrorMessage] = useState(null)
  
  //    
  const { analysisResult, analyzing, analyzeText } = useTextAnalysis()
  
  //     
  const [vocabularyProblems, setVocabularyProblems] = useState([])
  const [readingProblems, setReadingProblems] = useState([])
  const [generatedProblems, setGeneratedProblems] = useState([])
  const [selectedProblemCount] = useState({ vocabulary: 10, reading: 5 })
  
  //   
  const [selectedVocabulary, setSelectedVocabulary] = useState([])
  
  //    
  const [inputType, setInputType] = useState('paste')
  const [uploadedFile, setUploadedFile] = useState(null)
  const [urlInput, setUrlInput] = useState('')
  const [isProcessing, setIsProcessing] = useState(false)
  
  // API  
  const { execute: generateTextApi, loading: generatingText } = useApiCall(
    useCallback((params) => aiService.generateContent(params), [])
  )
  
  const { executeAll: generateProblemsApi, loading: generatingProblems } = useParallelApiCalls()
  
  // AI  
  const handleGenerateText = useCallback(async () => {
    setErrorMessage(null)
    setGeneratedText('') //   
    try {
      const topic = selectedTopic === 'custom' ? customTopic : selectedTopic
      const length = selectedLength === 'custom' ? customLength : selectedLength
      
      //   
      const gradeInfo = GRADE_OPTIONS.find(grade => grade.value === selectedGrade)
      const targetAge = gradeInfo ? gradeInfo.age : 10
      
      const prompt = `${topic}  ${selectedGrade}     ${length} .`
      
      const response = await generateTextApi({
        provider: selectedProvider,
        contentType: 'reading',
        prompt: topic,
        targetAge: targetAge,
        difficulty: 'intermediate',
        contentLength: parseInt(length)
      })
      
      setGeneratedText(typeof response.content === 'string' ? response.content : response.content.mainContent?.introduction || response.content)
      setStep(2)
    } catch (error) {
      console.error('  :', error)
      setErrorMessage(error.message || '  ')
    }
  }, [selectedTopic, customTopic, selectedGrade, selectedLength, customLength, generateTextApi])
  
  
  //   
  const handleAnalyzeText = useCallback(async () => {
    const textToAnalyze = mode === 'generate' ? generatedText : userText
    await analyzeText(textToAnalyze, selectedGrade)
  }, [mode, generatedText, userText, selectedGrade, analyzeText])
  
  //    
  const handleVocabularyChange = useCallback((vocabularyList) => {
    setSelectedVocabulary(vocabularyList)
  }, [])
  
  //   
  const handleProblemsChange = useCallback((problemsList) => {
    setGeneratedProblems(problemsList)
  }, [])
  
  //   
  const handleTextEdit = useCallback((newText) => {
    if (mode === 'generate') {
      setGeneratedText(newText)
    } else {
      setUserText(newText)
    }
  }, [mode])
  
  //    ()
  const handleFileUpload = useCallback(async (event) => {
    const file = event.target.files[0]
    if (!file) return
    
    const validation = validateFile(file)
    if (!validation.isValid) {
      setErrorMessage(validation.error)
      return
    }
    
    setUploadedFile(file)
    setErrorMessage(null)
    
    //   (   )
    if (file.type === 'text/plain') {
      const reader = new FileReader()
      reader.onload = (e) => {
        setUserText(e.target.result)
      }
      reader.readAsText(file)
    }
  }, [])
  
  //   
  const handleProcessUserInput = async () => {
    setIsProcessing(true)
    try {
      let textToProcess = userText
      
      if (inputType === 'file' && uploadedFile) {
        //   
        if (uploadedFile.type !== 'text/plain') {
          alert('   (.txt) .\n\n : DOCX, PDF ');
          return
        }
      } else if (inputType === 'url') {
        // URL   (   )
        alert(' URL    .\n\n       .');
        return
      }
      
      if (textToProcess.trim()) {
        setStep(2)
      }
    } catch (error) {
      console.error('  :', error)
      alert(`     .\n\n : ${error.message}\n\n .`);
    } finally {
      setIsProcessing(false)
    }
  }
  
  //   ( )
  const generateProblems = useCallback(async () => {
    setErrorMessage(null)
    try {
      const textToAnalyze = mode === 'generate' ? generatedText : userText
      
      //   
      const results = await generateProblemsApi([
        aiService.generateReadingProblems(
          textToAnalyze, 
          'vocabulary', 
          selectedProblemCount.vocabulary
        ),
        aiService.generateReadingProblems(
          textToAnalyze, 
          'comprehension', 
          selectedProblemCount.reading
        )
      ])
      
      if (results.success.length > 0) {
        //   
        results.success.forEach((response, index) => {
          if (response.success) {
            if (index === 0) {
              setVocabularyProblems(response.content.problems)
            } else {
              setReadingProblems(response.content.problems)
            }
          }
        })
      }
      
      //      
      if (results.hasErrors) {
        if (!vocabularyProblems.length) {
          setVocabularyProblems([
            {
              id: 1,
              word: '',
              question: '""    ?',
              options: ['1', '2', '3', '4'],
              answer: 0
            }
          ])
        }
        if (!readingProblems.length) {
          setReadingProblems([
            {
              id: 1,
              question: '    ?',
              options: ['1', '2', '3', '4'],
              answer: 0
            }
          ])
        }
      }
      
      setStep(3)
    } catch (error) {
      console.error('  :', error)
      setErrorMessage('    ')
    }
  }, [mode, generatedText, userText, selectedProblemCount, generateProblemsApi, vocabularyProblems, readingProblems])
  
  // PDF  
  const handlePDFDownload = useCallback(async () => {
    try {
      const textToExport = mode === 'generate' ? generatedText : userText
      const gradeLabel = GRADE_OPTIONS.find(g => g.value === selectedGrade)?.label || selectedGrade
      
      //  PDF  API 
      const response = await fetch(`${config.apiUrl}/api/pdf/generate`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          title: ' PRO  ',
          grade: gradeLabel,
          text: textToExport,
          analysisResult,
          selectedVocabulary: selectedVocabulary.filter(word => word.isChecked || word.selected),
          generatedProblems,
          vocabularyProblems,
          readingProblems
        })
      })

      if (!response.ok) {
        throw new Error(`HTTP error! status: ${response.status}`)
      }

      const result = await response.json()
      
      if (result.success) {
        // HTML     PDF    
        const printWindow = window.open('', '_blank', 'width=900,height=1200,scrollbars=yes')
        printWindow.document.write(result.htmlContent)
        printWindow.document.close()
        
        //       
        setTimeout(() => {
          printWindow.focus()
        }, 500)
      } else {
        throw new Error(result.error || 'PDF  ')
      }
    } catch (error) {
      console.error('PDF  :', error)
      setErrorMessage(`PDF    : ${error.message}`)
    }
  }, [mode, generatedText, userText, selectedGrade, analysisResult, selectedVocabulary, generatedProblems, vocabularyProblems, readingProblems])
  
  return (
    <div className="min-h-screen bg-gray-50">
      {/*  */}
      <header className="bg-white shadow-sm border-b">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="flex justify-between items-center py-4">
            <div className="flex items-center">
              <BookOpen className="h-8 w-8 text-blue-600" />
              <h1 className="ml-3 text-2xl font-bold text-gray-900"> PRO</h1>
              <span className="ml-3 px-3 py-1 bg-blue-100 text-blue-700 text-sm font-medium rounded-full">
                 
              </span>
            </div>
          </div>
        </div>
      </header>

      <main className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        {/*    */}
        {errorMessage && (
          <ErrorMessage 
            type="error"
            title=""
            message={errorMessage}
            onClose={() => setErrorMessage(null)}
          />
        )}
        
        {/*    */}
        <div className="mb-8">
          <div className="flex items-center justify-center space-x-4">
            <div className={`flex items-center ${step >= 1 ? 'text-blue-600' : 'text-gray-400'}`}>
              <div className={`w-10 h-10 rounded-full flex items-center justify-center ${
                step >= 1 ? 'bg-blue-600 text-white' : 'bg-gray-200'
              }`}>
                1
              </div>
              <span className="ml-2 font-medium"> </span>
            </div>
            <ArrowRight className="w-5 h-5 text-gray-400" />
            <div className={`flex items-center ${step >= 2 ? 'text-blue-600' : 'text-gray-400'}`}>
              <div className={`w-10 h-10 rounded-full flex items-center justify-center ${
                step >= 2 ? 'bg-blue-600 text-white' : 'bg-gray-200'
              }`}>
                2
              </div>
              <span className="ml-2 font-medium"> </span>
            </div>
            <ArrowRight className="w-5 h-5 text-gray-400" />
            <div className={`flex items-center ${step >= 3 ? 'text-blue-600' : 'text-gray-400'}`}>
              <div className={`w-10 h-10 rounded-full flex items-center justify-center ${
                step >= 3 ? 'bg-blue-600 text-white' : 'bg-gray-200'
              }`}>
                3
              </div>
              <span className="ml-2 font-medium"> </span>
            </div>
          </div>
        </div>

        {/* Step 1:   */}
        {step === 1 && (
          <div className="bg-white rounded-lg shadow-lg p-6">
            <h2 className="text-2xl font-bold mb-6"> </h2>
            
            {/*   */}
            <div className="grid grid-cols-2 gap-4 mb-8">
              <button
                onClick={() => setMode('generate')}
                className={`p-6 border-2 rounded-lg transition-all ${
                  mode === 'generate' 
                    ? 'border-blue-500 bg-blue-50' 
                    : 'border-gray-300 hover:border-gray-400'
                }`}
              >
                <Sparkles className="w-8 h-8 mx-auto mb-2 text-blue-600" />
                <h3 className="font-semibold mb-1">AI  </h3>
                <p className="text-sm text-gray-600">  AI  </p>
              </button>
              
              <button
                onClick={() => setMode('input')}
                className={`p-6 border-2 rounded-lg transition-all ${
                  mode === 'input' 
                    ? 'border-blue-500 bg-blue-50' 
                    : 'border-gray-300 hover:border-gray-400'
                }`}
              >
                <Upload className="w-8 h-8 mx-auto mb-2 text-blue-600" />
                <h3 className="font-semibold mb-1"> </h3>
                <p className="text-sm text-gray-600">   </p>
              </button>
            </div>

            {/* AI   */}
            {mode === 'generate' && (
              <div className="space-y-6">
                {/*   */}
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">
                     
                  </label>
                  <div className="grid grid-cols-3 gap-3">
                    {TOPIC_CATEGORIES.map(topic => (
                      <button
                        key={topic.id}
                        onClick={() => setSelectedTopic(topic.id)}
                        className={`p-3 border rounded-lg text-sm transition-all ${
                          selectedTopic === topic.id
                            ? 'border-blue-500 bg-blue-50 text-blue-700'
                            : 'border-gray-300 hover:border-gray-400'
                        }`}
                      >
                        {topic.label}
                      </button>
                    ))}
                  </div>
                  {selectedTopic === 'custom' && (
                    <input
                      type="text"
                      value={customTopic}
                      onChange={(e) => setCustomTopic(e.target.value)}
                      placeholder="  "
                      className="mt-3 w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500"
                    />
                  )}
                </div>

                {/*   */}
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">
                     
                  </label>
                  <select
                    value={selectedGrade}
                    onChange={(e) => setSelectedGrade(e.target.value)}
                    className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500"
                  >
                    {GRADE_OPTIONS.map(grade => (
                      <option key={grade.value} value={grade.value}>
                        {grade.label}
                      </option>
                    ))}
                  </select>
                </div>

                {/* AI   */}
                {availableProviders.length > 0 && (
                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-2">
                      AI  
                    </label>
                    <select
                      value={selectedProvider}
                      onChange={(e) => setSelectedProvider(e.target.value)}
                      className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500"
                    >
                      {availableProviders.map(provider => (
                        <option key={provider.id} value={provider.id}>
                          {provider.name}
                        </option>
                      ))}
                    </select>
                  </div>
                )}

                {/*   */}
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">
                     
                  </label>
                  <div className="grid grid-cols-4 gap-3">
                    {LENGTH_OPTIONS.map(length => (
                      <button
                        key={length.value}
                        onClick={() => setSelectedLength(length.value)}
                        className={`p-2 border rounded-lg text-sm transition-all ${
                          selectedLength === length.value
                            ? 'border-blue-500 bg-blue-50 text-blue-700'
                            : 'border-gray-300 hover:border-gray-400'
                        }`}
                      >
                        {length.label}
                      </button>
                    ))}
                  </div>
                  {selectedLength === 'custom' && (
                    <input
                      type="number"
                      value={customLength}
                      onChange={(e) => setCustomLength(e.target.value)}
                      placeholder="   (200-2000)"
                      min="200"
                      max="2000"
                      className="mt-3 w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500"
                    />
                  )}
                </div>

                {/* API    */}
                {availableProviders.length === 0 && (
                  <div className="bg-yellow-50 border border-yellow-200 rounded-lg p-4">
                    <p className="text-sm text-yellow-800 mb-2">
                      <strong>AI API   .</strong>
                    </p>
                    <p className="text-sm text-gray-700 mb-2">
                      AI      API  :
                    </p>
                    <ul className="text-sm text-gray-600 space-y-1 ml-4">
                      <li> OpenAI API : <a href="https://platform.openai.com/api-keys" target="_blank" rel="noopener noreferrer" className="text-blue-600 hover:underline">https://platform.openai.com/api-keys</a></li>
                      <li> Anthropic Claude API : <a href="https://console.anthropic.com/" target="_blank" rel="noopener noreferrer" className="text-blue-600 hover:underline">https://console.anthropic.com/</a></li>
                    </ul>
                    <p className="text-sm text-gray-600 mt-2">
                      Vercel   VITE_OPENAI_API_KEY  VITE_ANTHROPIC_API_KEY .
                    </p>
                  </div>
                )}

                {/*   */}
                <button
                  onClick={handleGenerateText}
                  disabled={!selectedTopic || generatingText || availableProviders.length === 0}
                  className="w-full py-3 bg-blue-600 text-white font-semibold rounded-lg hover:bg-blue-700 disabled:bg-gray-300 disabled:cursor-not-allowed transition-colors"
                >
                  {generatingText ? (
                    <span className="flex items-center justify-center">
                      <RefreshCw className="animate-spin mr-2" />
                        ...
                    </span>
                  ) : (
                    ' '
                  )}
                </button>
              </div>
            )}

            {/*    */}
            {mode === 'input' && (
              <div className="space-y-6">
                {/*    */}
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">
                      
                  </label>
                  <div className="grid grid-cols-3 gap-3">
                    <button
                      onClick={() => setInputType('paste')}
                      className={`p-3 border rounded-lg text-sm transition-all ${
                        inputType === 'paste'
                          ? 'border-blue-500 bg-blue-50 text-blue-700'
                          : 'border-gray-300 hover:border-gray-400'
                      }`}
                    >
                      <FileText className="w-5 h-5 mx-auto mb-1" />
                       
                    </button>
                    <button
                      onClick={() => setInputType('file')}
                      className={`p-3 border rounded-lg text-sm transition-all ${
                        inputType === 'file'
                          ? 'border-blue-500 bg-blue-50 text-blue-700'
                          : 'border-gray-300 hover:border-gray-400'
                      }`}
                    >
                      <Upload className="w-5 h-5 mx-auto mb-1" />
                       
                    </button>
                    <button
                      onClick={() => setInputType('url')}
                      className={`p-3 border rounded-lg text-sm transition-all ${
                        inputType === 'url'
                          ? 'border-blue-500 bg-blue-50 text-blue-700'
                          : 'border-gray-300 hover:border-gray-400'
                      }`}
                    >
                      <Globe className="w-5 h-5 mx-auto mb-1" />
                      URL 
                    </button>
                  </div>
                </div>

                {/*   */}
                {inputType === 'paste' && (
                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-2">
                       
                    </label>
                    <textarea
                      value={userText}
                      onChange={(e) => setUserText(e.target.value)}
                      placeholder="   ..."
                      className="w-full h-64 px-4 py-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 resize-none"
                    />
                    <div className="mt-2 text-sm text-gray-600">
                       : {userText.length}
                    </div>
                  </div>
                )}

                {/*   */}
                {inputType === 'file' && (
                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-2">
                       
                    </label>
                    <div className="mt-1 flex justify-center px-6 pt-5 pb-6 border-2 border-gray-300 border-dashed rounded-lg">
                      <div className="space-y-1 text-center">
                        <Upload className="mx-auto h-12 w-12 text-gray-400" />
                        <div className="flex text-sm text-gray-600">
                          <label
                            htmlFor="file-upload"
                            className="relative cursor-pointer bg-white rounded-md font-medium text-blue-600 hover:text-blue-500 focus-within:outline-none focus-within:ring-2 focus-within:ring-offset-2 focus-within:ring-blue-500"
                          >
                            <span> </span>
                            <input
                              id="file-upload"
                              name="file-upload"
                              type="file"
                              className="sr-only"
                              accept=".txt,.docx,.pdf"
                              onChange={handleFileUpload}
                            />
                          </label>
                          <p className="pl-1">   </p>
                        </div>
                        <p className="text-xs text-gray-500">
                          TXT, DOCX, PDF  
                        </p>
                      </div>
                    </div>
                    {uploadedFile && (
                      <div className="mt-3 p-3 bg-gray-50 rounded-lg">
                        <p className="text-sm font-medium"> : {uploadedFile.name}</p>
                        <p className="text-xs text-gray-500">: {(uploadedFile.size / 1024).toFixed(1)} KB</p>
                      </div>
                    )}
                  </div>
                )}

                {/* URL  */}
                {inputType === 'url' && (
                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-2">
                        URL
                    </label>
                    <input
                      type="url"
                      value={urlInput}
                      onChange={(e) => setUrlInput(e.target.value)}
                      placeholder="https://example.com/article"
                      className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500"
                    />
                    <p className="mt-1 text-xs text-gray-500">
                          
                    </p>
                  </div>
                )}

                {/*   (   ) */}
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">
                     
                  </label>
                  <select
                    value={selectedGrade}
                    onChange={(e) => setSelectedGrade(e.target.value)}
                    className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500"
                  >
                    {GRADE_OPTIONS.map(grade => (
                      <option key={grade.value} value={grade.value}>
                        {grade.label}
                      </option>
                    ))}
                  </select>
                </div>

                <button
                  onClick={handleProcessUserInput}
                  disabled={
                    (inputType === 'paste' && !userText.trim()) ||
                    (inputType === 'file' && !uploadedFile) ||
                    (inputType === 'url' && !urlInput.trim())
                  }
                  className="w-full py-3 bg-blue-600 text-white font-semibold rounded-lg hover:bg-blue-700 disabled:bg-gray-300 disabled:cursor-not-allowed transition-colors"
                >
                  {isProcessing ? (
                    <span className="flex items-center justify-center">
                      <RefreshCw className="animate-spin mr-2" />
                       ...
                    </span>
                  ) : (
                    ' '
                  )}
                </button>
              </div>
            )}
          </div>
        )}

        {/* Step 2:   */}
        {step === 2 && (
          <div className="bg-white rounded-lg shadow-lg p-6">
            <h2 className="text-2xl font-bold mb-6"> </h2>
            
            {/*   */}
            <TextDisplay 
              text={mode === 'generate' ? generatedText : userText}
              title=" "
              showCharCount={true}
              editable={true}
              onTextChange={handleTextEdit}
            />

            {/*   */}
            <div className="mb-6">
              <button
                onClick={handleAnalyzeText}
                disabled={analyzing}
                className="mb-4 px-6 py-2 bg-green-600 text-white font-medium rounded-lg hover:bg-green-700 disabled:bg-gray-300 disabled:cursor-not-allowed transition-colors"
              >
                {analyzing ? (
                  <span className="flex items-center">
                    <RefreshCw className="animate-spin mr-2" />
                     ...
                  </span>
                ) : (
                  '  '
                )}
              </button>

              {analysisResult && <AnalysisChart analysisResult={analysisResult} />}
            </div>

            {/*    */}
            <div className="mb-6">
              <VocabularyExtractor
                text={mode === 'generate' ? generatedText : userText}
                gradeLevel={selectedGrade}
                onVocabularyChange={handleVocabularyChange}
              />
            </div>

            {/*    */}
            <div className="mb-6">
              <ProblemGenerator
                text={mode === 'generate' ? generatedText : userText}
                gradeLevel={selectedGrade}
                onProblemsChange={handleProblemsChange}
              />
            </div>

            {/*    */}
            <div className="flex justify-between">
              <button
                onClick={() => setStep(1)}
                className="px-6 py-2 border border-gray-300 text-gray-700 font-medium rounded-lg hover:bg-gray-50 transition-colors"
              >
                 
              </button>
              <button
                onClick={generateProblems}
                disabled={generatingProblems}
                className="px-6 py-2 bg-blue-600 text-white font-medium rounded-lg hover:bg-blue-700 disabled:bg-gray-300 disabled:cursor-not-allowed transition-colors"
              >
                {generatingProblems ? (
                  <span className="flex items-center">
                    <RefreshCw className="animate-spin mr-2" />
                      ...
                  </span>
                ) : (
                  ' '
                )}
              </button>
            </div>
          </div>
        )}

        {/* Step 3:   */}
        {step === 3 && (
          <div className="bg-white rounded-lg shadow-lg p-6">
            <h2 className="text-2xl font-bold mb-6">  </h2>
            
            {/*   */}
            <div className="mb-8">
              <h3 className="text-lg font-semibold mb-4">  </h3>
              {vocabularyProblems.map((problem, index) => (
                <ProblemCard 
                  key={problem.id}
                  problem={problem}
                  index={index}
                  type="vocab"
                />
              ))}
            </div>

            {/*   */}
            <div className="mb-8">
              <h3 className="text-lg font-semibold mb-4">  </h3>
              {readingProblems.map((problem, index) => (
                <ProblemCard 
                  key={problem.id}
                  problem={problem}
                  index={index}
                  type="reading"
                />
              ))}
            </div>

            {/* PDF   */}
            <div className="flex justify-between">
              <button
                onClick={() => setStep(2)}
                className="px-6 py-2 border border-gray-300 text-gray-700 font-medium rounded-lg hover:bg-gray-50 transition-colors"
              >
                 
              </button>
              <button
                onClick={handlePDFDownload}
                className="flex items-center px-6 py-2 bg-green-600 text-white font-medium rounded-lg hover:bg-green-700 transition-colors"
              >
                <Download className="mr-2" />
                PDF 
              </button>
            </div>
          </div>
        )}
      </main>
    </div>
  )
}