import React, { memo } from 'react'
import ProgressBar from '../common/ProgressBar'

/**
 *     ()
 * @param {object} analysisResult -   
 */
const AnalysisChart = memo(function AnalysisChart({ analysisResult }) {
  if (!analysisResult) return null

  const analysisItems = [
    { label: ' ', score: analysisResult.textLength },
    { label: ' ', score: analysisResult.vocabularyLevel },
    { label: '  ', score: analysisResult.sentenceComplexity },
    { label: '  ', score: analysisResult.contentLevel },
    { label: ' ', score: analysisResult.backgroundKnowledge }
  ]

  return (
    <div className="space-y-4">
      <h3 className="text-lg font-semibold">   </h3>
      
      {/*    */}
      <div className="space-y-3">
        {analysisItems.map((item, index) => (
          <ProgressBar
            key={index}
            label={item.label}
            value={item.score}
            max={10}
            color="blue"
            size="medium"
          />
        ))}
      </div>

      {/*  */}
      <div className="mt-4 p-4 bg-blue-50 rounded-lg">
        <div className="flex items-center justify-between">
          <span className="font-semibold text-lg"> </span>
          <span className="text-2xl font-bold text-blue-600">
            {analysisResult.totalScore}/10
          </span>
        </div>
      </div>
    </div>
  )
})

export default AnalysisChart