import React, { useState, useEffect } from 'react'
import { supabase } from '../../lib/supabase'

const TemplateManager = () => {
  const [templates, setTemplates] = useState([])
  const [showModal, setShowModal] = useState(false)
  const [editingTemplate, setEditingTemplate] = useState(null)
  const [loading, setLoading] = useState(true)

  useEffect(() => {
    fetchTemplates()
  }, [])

  const fetchTemplates = async () => {
    try {
      const token = localStorage.getItem('adminToken')
      const response = await fetch('/api/admin/templates', {
        headers: {
          'Authorization': `Bearer ${token}`
        }
      })
      const data = await response.json()
      if (data.success) {
        setTemplates(data.templates)
      }
    } catch (error) {
      console.error('Failed to fetch templates:', error)
    } finally {
      setLoading(false)
    }
  }

  const handleEdit = (template) => {
    setEditingTemplate(template)
    setShowModal(true)
  }

  const handleDelete = async (id) => {
    if (!confirm('   ?')) return

    try {
      const token = localStorage.getItem('adminToken')
      const response = await fetch(`/api/admin/templates/${id}`, {
        method: 'DELETE',
        headers: {
          'Authorization': `Bearer ${token}`
        }
      })
      
      if (response.ok) {
        fetchTemplates()
      }
    } catch (error) {
      console.error('Failed to delete template:', error)
    }
  }

  if (loading) {
    return <div className="flex justify-center items-center h-64"> ...</div>
  }

  return (
    <div className="p-6">
      <div className="flex justify-between items-center mb-6">
        <h2 className="text-2xl font-bold"> </h2>
        <button
          onClick={() => {
            setEditingTemplate(null)
            setShowModal(true)
          }}
          className="bg-blue-600 text-white px-4 py-2 rounded-lg hover:bg-blue-700"
        >
            
        </button>
      </div>

      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
        {templates.map(template => (
          <TemplateCard
            key={template.id}
            template={template}
            onEdit={handleEdit}
            onDelete={handleDelete}
          />
        ))}
      </div>

      {showModal && (
        <TemplateModal
          template={editingTemplate}
          onClose={() => setShowModal(false)}
          onSave={() => {
            setShowModal(false)
            fetchTemplates()
          }}
        />
      )}
    </div>
  )
}

const TemplateCard = ({ template, onEdit, onDelete }) => {
  const contentTypeLabels = {
    reading: ' ',
    vocabulary: ' ',
    grammar: ' ',
    quiz: '',
    questions: '',
    answers: ''
  }

  const difficultyLabels = {
    beginner: '',
    intermediate: '',
    advanced: ''
  }

  return (
    <div className="bg-white rounded-lg shadow-md p-4 hover:shadow-lg transition-shadow">
      <h3 className="font-bold text-lg mb-2">{template.title}</h3>
      <div className="space-y-2 text-sm text-gray-600">
        <p>: {contentTypeLabels[template.content_type]}</p>
        <p>: {difficultyLabels[template.difficulty]}</p>
        <p>: {template.target_age}</p>
      </div>
      <div className="mt-4 flex justify-end space-x-2">
        <button
          onClick={() => onEdit(template)}
          className="text-blue-600 hover:text-blue-800"
        >
          
        </button>
        <button
          onClick={() => onDelete(template.id)}
          className="text-red-600 hover:text-red-800"
        >
          
        </button>
      </div>
    </div>
  )
}

const TemplateModal = ({ template, onClose, onSave }) => {
  const [formData, setFormData] = useState({
    title: template?.title || '',
    content_type: template?.content_type || 'reading',
    difficulty: template?.difficulty || 'intermediate',
    target_age: template?.target_age || 'adult',
    template_prompt: template?.template_prompt || '',
    variables: template?.variables || []
  })

  const handleSubmit = async (e) => {
    e.preventDefault()
    
    try {
      const token = localStorage.getItem('adminToken')
      const url = template 
        ? `/api/admin/templates/${template.id}`
        : '/api/admin/templates'
      
      const response = await fetch(url, {
        method: template ? 'PUT' : 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Authorization': `Bearer ${token}`
        },
        body: JSON.stringify(formData)
      })

      if (response.ok) {
        onSave()
      }
    } catch (error) {
      console.error('Failed to save template:', error)
    }
  }

  const addVariable = () => {
    setFormData({
      ...formData,
      variables: [...formData.variables, { name: '', description: '' }]
    })
  }

  return (
    <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center p-4">
      <div className="bg-white rounded-lg max-w-2xl w-full max-h-90vh overflow-y-auto p-6">
        <h3 className="text-xl font-bold mb-4">
          {template ? ' ' : '  '}
        </h3>
        
        <form onSubmit={handleSubmit} className="space-y-4">
          <div>
            <label className="block text-sm font-medium mb-1"> </label>
            <input
              type="text"
              value={formData.title}
              onChange={(e) => setFormData({ ...formData, title: e.target.value })}
              className="w-full px-3 py-2 border rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500"
              required
            />
          </div>

          <div>
            <label className="block text-sm font-medium mb-1"> </label>
            <select
              value={formData.content_type}
              onChange={(e) => setFormData({ ...formData, content_type: e.target.value })}
              className="w-full px-3 py-2 border rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500"
            >
              <option value="reading"> </option>
              <option value="vocabulary"> </option>
              <option value="grammar"> </option>
              <option value="quiz"></option>
              <option value="questions"></option>
              <option value="answers"></option>
            </select>
          </div>

          <div>
            <label className="block text-sm font-medium mb-1"></label>
            <select
              value={formData.difficulty}
              onChange={(e) => setFormData({ ...formData, difficulty: e.target.value })}
              className="w-full px-3 py-2 border rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500"
            >
              <option value="beginner"></option>
              <option value="intermediate"></option>
              <option value="advanced"></option>
            </select>
          </div>

          <div>
            <label className="block text-sm font-medium mb-1"> </label>
            <input
              type="text"
              value={formData.target_age}
              onChange={(e) => setFormData({ ...formData, target_age: e.target.value })}
              className="w-full px-3 py-2 border rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500"
              placeholder=": adult, elem1, middle2"
              required
            />
          </div>

          <div>
            <label className="block text-sm font-medium mb-1"> </label>
            <textarea
              value={formData.template_prompt}
              onChange={(e) => setFormData({ ...formData, template_prompt: e.target.value })}
              className="w-full px-3 py-2 border rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500"
              rows={8}
              placeholder="{{topic}}  {{difficulty}}     {{length}} ..."
              required
            />
            <p className="text-sm text-gray-600 mt-1">
               {'{{}}'}  .
            </p>
          </div>

          <div>
            <div className="flex justify-between items-center mb-2">
              <label className="block text-sm font-medium"> </label>
              <button
                type="button"
                onClick={addVariable}
                className="text-blue-600 hover:text-blue-800 text-sm"
              >
                 
              </button>
            </div>
            {formData.variables.map((variable, index) => (
              <div key={index} className="flex space-x-2 mb-2">
                <input
                  type="text"
                  placeholder=""
                  value={variable.name}
                  onChange={(e) => {
                    const newVars = [...formData.variables]
                    newVars[index].name = e.target.value
                    setFormData({ ...formData, variables: newVars })
                  }}
                  className="flex-1 px-3 py-2 border rounded-lg"
                />
                <input
                  type="text"
                  placeholder=""
                  value={variable.description}
                  onChange={(e) => {
                    const newVars = [...formData.variables]
                    newVars[index].description = e.target.value
                    setFormData({ ...formData, variables: newVars })
                  }}
                  className="flex-1 px-3 py-2 border rounded-lg"
                />
                <button
                  type="button"
                  onClick={() => {
                    const newVars = formData.variables.filter((_, i) => i !== index)
                    setFormData({ ...formData, variables: newVars })
                  }}
                  className="text-red-600 hover:text-red-800"
                >
                  
                </button>
              </div>
            ))}
          </div>

          <div className="flex justify-end space-x-3 pt-4">
            <button
              type="button"
              onClick={onClose}
              className="px-4 py-2 text-gray-600 hover:text-gray-800"
            >
              
            </button>
            <button
              type="submit"
              className="px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700"
            >
              {template ? '' : ''}
            </button>
          </div>
        </form>
      </div>
    </div>
  )
}

export default TemplateManager