import React, { memo, useMemo } from 'react'
import EditableText from '../common/EditableText'

/**
 *    ( +  )
 * @param {string} text -  
 * @param {string} title - 
 * @param {boolean} showCharCount -    
 * @param {string} className -  CSS 
 * @param {boolean} editable -   
 * @param {function} onTextChange -   
 */
const TextDisplay = memo(function TextDisplay({ 
  text = '', 
  title = ' ', 
  showCharCount = true,
  className = '',
  editable = false,
  onTextChange
}) {
  //    ()
  const charCount = useMemo(() => text.length, [text])
  
  //    ()
  const sentenceCount = useMemo(() => {
    const sentences = text.split(/[.!?]/).filter(s => s.trim())
    return sentences.length
  }, [text])

  return (
    <div className={`mb-6 ${className}`}>
      <h3 className="text-lg font-semibold mb-3">{title}</h3>
      <div className="bg-gray-50 rounded-lg">
        {editable ? (
          <EditableText
            value={text}
            onSave={onTextChange}
            type="textarea"
            rows={10}
            placeholder=" ..."
            className="w-full"
            editClassName="text-lg leading-relaxed"
            displayClassName="p-4"
            renderDisplay={(displayValue) => (
              <p className="text-lg leading-relaxed whitespace-pre-wrap p-4">
                {displayValue}
              </p>
            )}
          />
        ) : (
          <p className="text-lg leading-relaxed whitespace-pre-wrap p-4">
            {text}
          </p>
        )}
      </div>
      {showCharCount && (
        <div className="mt-2 flex items-center justify-between text-sm text-gray-600">
          <span> : {charCount}</span>
          <span> : {sentenceCount}</span>
        </div>
      )}
    </div>
  )
})

export default TextDisplay