// Client-side AI Service for direct API calls
import { apiKeys } from '../config/apiKeys'
import { isAllowedEnvironment, apiRateLimiter, sanitizeApiResponse, logApiUsage } from '../utils/security'

class ClientAIService {
  constructor() {
    this.providers = {
      openai: {
        url: 'https://api.openai.com/v1/chat/completions',
        headers: (apiKey) => ({
          'Content-Type': 'application/json',
          'Authorization': `Bearer ${apiKey}`
        }),
        formatRequest: (prompt) => ({
          model: 'gpt-3.5-turbo',
          messages: [{
            role: 'user',
            content: prompt
          }],
          temperature: 0.7
        }),
        parseResponse: (data) => ({
          success: true,
          content: data.choices?.[0]?.message?.content || '',
          metadata: {
            provider: 'openai',
            model: data.model,
            tokensUsed: data.usage?.total_tokens || 0
          }
        })
      },
      anthropic: {
        // Anthropic API CORS     
        //    , Vercel Edge Function  
        url: '/api/anthropic', //  
        headers: (apiKey) => ({
          'Content-Type': 'application/json',
          'x-api-key': apiKey,
          'anthropic-version': '2023-06-01'
        }),
        formatRequest: (prompt) => ({
          model: 'claude-3-sonnet-20240229',
          max_tokens: 4000,
          messages: [{
            role: 'user',
            content: prompt
          }]
        }),
        parseResponse: (data) => ({
          success: true,
          content: data.content?.[0]?.text || '',
          metadata: {
            provider: 'anthropic',
            model: data.model,
            tokensUsed: data.usage?.total_tokens || 0
          }
        })
      }
    }
  }

  async generateContent(request) {
    try {
      //  
      if (!isAllowedEnvironment()) {
        throw new Error('   API .')
      }

      // Rate limiting 
      if (!apiRateLimiter.canMakeRequest()) {
        const remainingTime = Math.ceil(apiRateLimiter.getRemainingTime() / 1000)
        throw new Error(`API   . ${remainingTime}   .`)
      }

      const { provider = 'openai', prompt } = request
      const apiKey = apiKeys[provider]
      
      if (!apiKey) {
        throw new Error(`${provider} API   .`)
      }

      const providerConfig = this.providers[provider]
      if (!providerConfig) {
        throw new Error(`  AI : ${provider}`)
      }

      const response = await fetch(providerConfig.url, {
        method: 'POST',
        headers: providerConfig.headers(apiKey),
        body: JSON.stringify(providerConfig.formatRequest(prompt))
      })

      if (!response.ok) {
        const errorData = await response.json()
        throw new Error(errorData.error?.message || `API  : ${response.status}`)
      }

      const data = await response.json()
      const result = providerConfig.parseResponse(data)
      
      // Sanitize response
      if (result.content && typeof result.content === 'string') {
        result.content = sanitizeApiResponse(result.content)
      }
      
      // Log usage
      logApiUsage(provider, true)
      
      return result

    } catch (error) {
      console.error('Client AI Service Error:', error)
      logApiUsage(request.provider || 'openai', false, error)
      throw error
    }
  }

  //       
  async generateReadingText(topic, grade, length, provider = 'openai') {
    const prompt = `      :
    
: ${topic}
 : ${grade}
:  ${length}

     :
1.        
2.   
3.      
4.    

     .`

    const response = await this.generateContent({ provider, prompt })
    
    //   
    let content = response.content
    if (typeof content === 'string') {
      content = content.trim()
      //   
      const lines = content.split('\n')
      const title = lines[0]?.replace(/^#+ /, '') || `${topic} `
      const mainContent = lines.slice(1).join('\n').trim() || content
      
      response.content = {
        title,
        mainContent: {
          introduction: mainContent
        }
      }
    }
    
    return response
  }

  //  
  async analyzeReadingLevel(text, gradeLevel, provider = 'openai') {
    const prompt = `    :

: """
${text}
"""

 : ${gradeLevel}

 5   1-10    :
1.    (textLength)
2.   (vocabularyLevel)
3.   (sentenceComplexity)
4.   (contentLevel)
5.   (backgroundKnowledge)

 :
{
  "textLength": ,
  "vocabularyLevel": ,
  "sentenceComplexity": ,
  "contentLevel": ,
  "backgroundKnowledge": ,
  "totalScore": ,
  "analysis": "  "
}`

    const response = await this.generateContent({ provider, prompt })
    
    try {
      // JSON  
      const content = response.content
      const jsonMatch = content.match(/\{[\s\S]*\}/)
      if (jsonMatch) {
        const parsed = JSON.parse(jsonMatch[0])
        response.content = parsed
      } else {
        //    
        response.content = {
          textLength: 5,
          vocabularyLevel: 5,
          sentenceComplexity: 5,
          contentLevel: 5,
          backgroundKnowledge: 5,
          totalScore: 5,
          analysis: '  .'
        }
      }
    } catch (e) {
      console.error('   :', e)
    }
    
    return response
  }

  //  
  async extractVocabulary(text, gradeLevel, count = 10, provider = 'openai') {
    const prompt = `  ${gradeLevel}      ${count} :

: """
${text}
"""

     :
- word: 
- meaning:   (  )
- difficulty:  ( 1-5)
- example: 

JSON   .`

    const response = await this.generateContent({ provider, prompt })
    
    try {
      const content = response.content
      const jsonMatch = content.match(/\[[\s\S]*\]/)
      if (jsonMatch) {
        const vocabularyList = JSON.parse(jsonMatch[0])
        response.content = { vocabularyList }
      } else {
        //    
        response.content = {
          vocabularyList: [
            { word: '', meaning: '  ', difficulty: '', example: '   .' }
          ]
        }
      }
    } catch (e) {
      console.error('   :', e)
    }
    
    return response
  }

  //  
  async generateProblems(text, problemType, count, provider = 'openai') {
    const typeMap = {
      vocabulary: '',
      comprehension: '',
      inference: '',
      critical: ' '
    }
    
    const prompt = `   ${typeMap[problemType] || problemType}  ${count} :

: """
${text}
"""

    :
- id:  
- type: "${problemType}"
- question: 
- options: 4  
- answer:   (0-3)
- explanation: 

JSON   .`

    const response = await this.generateContent({ provider, prompt })
    
    try {
      const content = response.content
      const jsonMatch = content.match(/\[[\s\S]*\]/)
      if (jsonMatch) {
        const problems = JSON.parse(jsonMatch[0])
        response.content = { problems }
      } else {
        //     
        response.content = {
          problems: [{
            id: 1,
            type: problemType,
            question: '   ?',
            options: [' 1', ' 2', ' 3', ' 4'],
            answer: 0,
            explanation: '    .'
          }]
        }
      }
    } catch (e) {
      console.error('   :', e)
    }
    
    return response
  }
}

export default new ClientAIService()